import { Injectable } from '@angular/core';
import { AngularFirestore} from '@angular/fire/firestore';
import { FirebaseAuthentication } from '@ionic-native/firebase-authentication/ngx';
import { Observable } from 'rxjs';
import * as firebase from 'firebase';

export interface Comida {
  fecha: string;
  userID: any;
  comida: string;
  nombre: string;
  ingredientes: string;
  notas: string;
  calorias: number;
}

@Injectable({
  providedIn: 'root'
})
export class FirebaseService {

  nuevaComida: Comida;

  usuario: Observable<string>;

  constructor(private firestore: AngularFirestore, private user: FirebaseAuthentication) {

  }

  loginUserId(email, contrasena) {

    // All future sign-in request now include tenant ID.
    firebase.auth().signInWithEmailAndPassword(email, contrasena)
      .then(function(result) {
         return result.user.uid
        // result.user.tenantId should be ‘TENANT_PROJECT_ID’.
      }).catch(function(error) {
        // Handle error.
      });
        
      }

  agregarComidaService(fecha: string, userID: string, comida: string, nombre: string, ingredientes: string, notas: string, calorias: number) {
      
    this.nuevaComida = {
    fecha: fecha,
    userID: userID,
    comida: comida,
    nombre: nombre,
    ingredientes: ingredientes,
    notas: notas,
    calorias: calorias
  };

    this.firestore.collection<Comida>('comidasGuardadas').add(this.nuevaComida);
}


}

