import { PopoverController } from '@ionic/angular';
import { Component, OnInit} from '@angular/core';
import { FechaService } from '../services/fecha.service';
import { FirebaseService } from '../services/firebase.service';


@Component({
  selector: 'app-popover-agregar-comida',
  templateUrl: './popover-agregar-comida.component.html',
  styleUrls: ['./popover-agregar-comida.component.scss'],
})


export class PopoverAgregarComidaComponent implements OnInit {

  fechaElegida = this.fs.fecha;
  comida: string;
  nombre: string;
  ingredientes: string;
  notas: string;
  calorias: number;
  idUsuario: any;

  constructor(private fs: FechaService, private popover: PopoverController, private fbs: FirebaseService) {
    
  }
  
    ngOnInit() {

  }

    agregarComida() {
      this.idUsuario = this.fbs.loginUserId('enmanuelfeliz106@gmail.com', 'universal0707');
      // tslint:disable-next-line: max-line-length
      this.fbs.agregarComidaService(this.fechaElegida, this.idUsuario, this.comida, this.nombre, this.ingredientes, this.notas, this.calorias);

      this.popover.dismiss();

  }

  cerrarPopover() {
    this.comida = '';
    this.nombre = '';
    this.ingredientes = '';
    this.notas = '';
    this.calorias = 0;
    
    this.popover.dismiss();
  }


}
